import React, { FC, useCallback, useEffect, useMemo } from 'react';
import { Checkbox, Col, DatePicker, Radio, Row, Typography } from 'antd';
import cn from 'classnames';
import { RadioChangeEvent } from 'antd/lib/radio/interface';
import moment, { Moment } from 'moment';
import { CloseOutlined } from '@ant-design/icons';
import { AppRadioBtn } from 'components/ui/AppRadioBtn';
import { useTypedSelector } from 'hooks/useTypesSelector';
import { ratesSelector } from 'store/selectors/selectors';
import { useActions } from 'hooks/useActions';
import ErrorLoading from 'components/ui/ErrorLoading/ErrorLoading';
import { IFormAdditionally } from './type';
import styles from './styles.module.less';

const { Text } = Typography;

export const FormAdditionally: FC<IFormAdditionally> = ({
  carColors,
  color,
  setColor,
  startDate,
  setStartDate,
  endDate,
  setEndDate,
  rate,
  setRate,
  setRatePrice,
  isFullTank,
  setIsFullTank,
  isChildSeat,
  setIsChildSeat,
  isRightHandDrive,
  setIsRightHandDrive,
}) => {
  // 
  const { rates, ratesIsLoading, ratesError } = useTypedSelector(ratesSelector);

  //       api
  const { fetchRates } = useActions();
  useEffect(() => {
    fetchRates();
  }, []);

  //  ,   .
  //   ,     api.
  const filteredRates = useMemo(() => {
    return rates.filter((item) => item.rateTypeId);
  }, [rates]);

  //   
  const filteredColors = useMemo(() => {
    return Array.from(new Set(carColors));
  }, [carColors]);

  //    color radio button
  const colorChangeHandler = useCallback(
    (event: RadioChangeEvent) => {
      setColor(event.target.value);
    },
    [color]
  );

  //  ,   
  const disabledStartDate = useCallback((current: Moment) => {
    return current < moment();
  }, []);

  //  ,   .   ,    
  const disabledEndDate = useCallback(
    (current: Moment) => {
      return current < moment(startDate);
    },
    [startDate]
  );

  //    
  const startDateChangeHandler = useCallback(
    (date: Moment | null) => {
      setEndDate(undefined);
      return setStartDate(date!);
    },
    [startDate]
  );

  const endDateChangeHandler = useCallback(
    (date: Moment | null) => {
      return setEndDate(date!);
    },
    [endDate]
  );

  //    rate radio button
  const rateChangeHandler = useCallback(
    (value, price) => {
      setRate(value);
      setRatePrice(price);
    },
    [rate]
  );

  //    checkbox
  const fullTankChangeHandler = useCallback(() => {
    setIsFullTank(!isFullTank);
  }, [isFullTank]);

  const childSeatChangeHandler = useCallback(() => {
    setIsChildSeat(!isChildSeat);
  }, [isChildSeat]);

  const rightHandDriveChangeHandler = useCallback(() => {
    setIsRightHandDrive(!isRightHandDrive);
  }, [isRightHandDrive]);

  return (
    <div>
      {ratesIsLoading ? (
        <ErrorLoading loading={ratesIsLoading} error={ratesError} />
      ) : (
        <div className={styles.FormAdditionally}>
          <div className={styles.colorsBlock}>
            <Text className={styles.text__light}></Text>
            <div className={styles.colorsContainer}>
              <Radio.Group
                onChange={colorChangeHandler}
                value={color}
                className={cn(styles.RadioGroup, {
                  [styles.RadioFixedWidth]: filteredColors.length >= 4,
                })}
              >
                {filteredColors.map((carColor) => {
                  return (
                    <AppRadioBtn key={carColor} value={carColor} filterValue={color}>
                      {carColor}
                    </AppRadioBtn>
                  );
                })}
              </Radio.Group>
            </div>
          </div>
          <div className={styles.dateBlock}>
            <Text className={styles.text__light}> </Text>
            <div className={styles.dates}>
              <div className={styles.dateContainer}>
                <Text className={cn(styles.text__light, styles.dateText)}></Text>
                <DatePicker
                  format="DD.MM.YYYY HH:mm"
                  defaultValue={moment()}
                  showTime
                  showNow={false}
                  className={styles.datePicker}
                  disabledDate={disabledStartDate}
                  value={startDate}
                  onChange={startDateChangeHandler}
                  placeholder="   "
                  clearIcon={<CloseOutlined style={{ color: '#000', fontSize: 14 }} />}
                  suffixIcon
                />
              </div>
              <div className={styles.dateContainer}>
                <Text className={cn(styles.text__light, styles.dateText)}></Text>
                <DatePicker
                  format="DD.MM.YYYY HH:mm"
                  showTime
                  showNow={false}
                  className={styles.datePicker}
                  disabledDate={disabledEndDate}
                  value={endDate}
                  onChange={endDateChangeHandler}
                  placeholder="   "
                  clearIcon={<CloseOutlined style={{ color: '#000', fontSize: 14 }} />}
                  suffixIcon
                  disabled={!startDate}
                />
              </div>
            </div>
          </div>
          <div className={styles.ratesBlock}>
            <Text className={styles.text__light}></Text>
            <div className={styles.ratesContainer}>
              <div className={styles.RadioGroup}>
                {filteredRates.map((item) => {
                  return (
                    <AppRadioBtn
                      key={item.id}
                      value={item.rateTypeId!.name}
                      filterValue={rate}
                      onClick={(event) => rateChangeHandler(item.rateTypeId!.name, item.price)}
                    >
                      {item.rateTypeId!.name}, {item.price}/{item.rateTypeId!.unit}
                    </AppRadioBtn>
                  );
                })}
              </div>
            </div>
          </div>
          <div className={styles.servicesBlock}>
            <Text className={styles.text__light}> </Text>
            <div className={styles.servicesContainer}>
              <div className={styles.checkboxGroup}>
                <Row>
                  <Col span={24}>
                    <Checkbox onChange={fullTankChangeHandler} checked={isFullTank}>
                       , 500
                    </Checkbox>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Checkbox onChange={childSeatChangeHandler} checked={isChildSeat}>
                       , 200
                    </Checkbox>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Checkbox onChange={rightHandDriveChangeHandler} checked={isRightHandDrive}>
                       , 1600
                    </Checkbox>
                  </Col>
                </Row>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
